:root {
  --main-color: #5ea3d0;
  --secondary-color: white;
  --main-text-color: #3e5869;
  --secondary-text-color: #b0c7d6;
  --send-message-form: #f5f5f5;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-weight: 180;
  color: #3e5869;
}

#root {
  height: 100%;
}

.App {
  display: grid;
  height: 100%;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 60px;
  grid-template-areas:
    "r m m m m m"
    "r m m m m m"
    "r m m m m m"
    "r m m m m m"
    "r m m m m m"
    "n s s s s s";
}

.new-room-form {
  grid-area: n;
}
.rooms-list {
  grid-area: r;
}
.message-list {
  grid-area: m;
}
.send-message-form {
  grid-area: s;
}

.rooms-list {
  box-sizing: border-box;
  padding: 10px;
  background-color: #5ea3d0;
  background-color: var(--main-color);
  overflow: scroll;
  height: 100%;
}

.rooms-list ul {
  list-style-type: none;
  padding: 0;
}

.rooms-list li {
  margin: 10px 0;
}

.rooms-list h3 {
  margin: 5px 0;
  color: white;
  color: var(--secondary-color);
}

.rooms-list .room a {
  color: #b0c7d6;
  color: var(--secondary-text-color);
  font-weight: 550;
  text-decoration: none;
}

.rooms-list .room.active a {
  color: white;
  color: var(--secondary-color);
}

.new-room-form {
  padding: 0 5px;
  background: white;
  background: var(--secondary-color);
  color: #3e5869;
  color: var(--main-text-color);
}

.new-room-form form {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.new-room-form input {
  width: 135px;
  background: white;
  background: var(--secondary-color);
}

.new-room-form button {
  background: white;
  background: var(--secondary-color);
  color: #3e5869;
  color: var(--main-text-color);
  border: 0;
}

.new-room-form button {
  background: white;
  background: var(--secondary-color);
  color: #3e5869;
  color: var(--main-text-color);
  border: 0;
}

.new-room-form input::-webkit-input-placeholder {
  color: #3e5869;
  color: var(--main-text-color);
  font-weight: 200;
}

.new-room-form input::-ms-input-placeholder {
  color: #3e5869;
  color: var(--main-text-color);
  font-weight: 200;
}

.new-room-form input::placeholder {
  color: #3e5869;
  color: var(--main-text-color);
  font-weight: 200;
}

.new-room-form input:focus {
  outline-width: 0;
}

.new-room-form input {
  border: 0;
}

.new-room-form button {
  border: 0;
}

.message {
  margin: 15px 0;
}

.message .message-username {
  font-size: 11px;
  color: #3e5869;
  color: var(--main-text-color);
  opacity: 0.9;
  margin-bottom: 6px;
}

.message .message-text {
  background: #5ea3d0;
  background: var(--main-color);
  color: white;
  color: var(--secondary-color);
  display: inline;
  padding: 4px 8px;
  border-radius: 8px;
}

.message-list {
  box-sizing: border-box;
  padding-left: 6px;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background: white;
  background: var(--secondary-color);
}

.message-list .join-room {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  font-size: 34px;
  font-weight: 300;
}

.send-message-form {
  background: #f5f5f5;
  background: var(--send-message-form);
  display: -webkit-flex;
  display: flex;
}

.send-message-form input {
  width: 100%;
  padding: 15px 10px;
  margin: 0;
  border-style: none;
  background: #f5f5f5;
  background: var(--send-message-form);
  font-weight: 200;
}

.send-message-form input:focus {
  outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}

.send-message-form input::placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}

.help-text {
  position: absolute;
  top: 10px;
}


